<template>
  <div id="app">
    <header class="header">
      <h1>Fractalite Workbench</h1>
      <v-select :options="options" v-model="component" placeholder="Select a component..."></v-select>
    </header>
    <iframe class="preview" :src="preview" width="100%" height="100%" v-if="component"></iframe>
  </div>
</template>

<script>
import { sortBy } from 'lodash';

export default {
  name: 'app',
  data() {
    return {
      component: null
    };
  },
  computed: {
    options() {
      return sortBy(
        this.$store.state.components.map(component => ({
          value: component.name,
          label: component.label
        })),
        'label'
      );
    },
    preview() {
      if (this.component) {
        return `preview/${this.component.value}`;
      }
      return null;
    }
  },
  methods: {
    selectComponent(component) {
      this.component = component;
    }
  },

  mounted() {
    this.$store.dispatch('initialise');
  }
};
</script>

